<template>
  <div>
    <h1 class="tittle">{{articleShow.title}}</h1>
    <!--发表时间-->
    <div class="time">
    </div>
    <article v-html="article" class="markdown-body"></article>
    <el-backtop target=".page"></el-backtop>
    <div id="gitalk-container"></div>
  </div>
</template>

<script>
  import {getArticle} from "../../../service/api";
  import marked from "marked";
  import Gitalk from "gitalk";
  import md5 from "md5";

  export default {
    name: "Articles",
    props: ["id"],
    data() {
      return {
        articleShow: []
      };
    },
    computed: {
      article() {
        let article = this.articleShow.article;
        try {
          return marked(article);
        } catch (e) {
        }

      }
    },
    created() {
      let params = {
        id: this.id
      };
      //请求网络数据
      getArticle(params)
        .then(response => {
          this.articleShow = response.data;
        })
        .catch(error => {
          console.log(error);
        });

    },
mounted() {
  const gitalk = new Gitalk({
    clientID: "8286c08e74ee986775c5",
    clientSecret: "8356019339a297c7df171582299c3a7c6fef6957",
    repo: "qflyb.github.io",      // The repository of store comments,
    owner: "qflyb",
    admin: "qflyb",
    id: md5(location.hash),      // Ensure uniqueness and length less than 50
    distractionFreeMode: false  // Facebook-like distraction free mode
  });
  gitalk.render("gitalk-container");
}
  };

</script>

<style lang="scss" scoped>
  @import "../../../style/statement";
  @import "../../../style/markdown.css";

  .tittle {
    font-size: 30px;
    margin: 30px 0;
    padding: 10px 0;
  }

  .time {
    line-height: 60px;
    margin: 30px 0;
  }

  @include styleAllPhone {
    .tittle {
      font-size: 25px;
    }
  }

</style>
